.grid{
    width:100%;
    overflow-x: hidden;
    position: relative;
    display: grid;
    grid-template-areas: "dairy todo notes" "dairy events notes";
}
.grid section{
    display: flex;
    justify-content:flex-start;
    flex-direction: column;
    position: relative;
}
.dairy,.events,.todo,.notes{
    padding:10px;
}
.todo,.events{
    height:50vh;
    min-height:300px;
}

/* todo  */

.todo{
    width:calc(100vw / 3);
    grid-area: todo;
}
.todo .addTodo p{
    font-size:20px;
    color:#4ea6b8;
}
.todo .addTodo div{
    margin:10px 0;
}
.todo .addTodo div input{
    font-size:13px;
    outline:none;
    border:none;
    background-color: whitesmoke;
    padding:10px;
    border-radius:5px;
    display: inline-block;
    width:calc(100% - 50px);
    max-width:300px;
}
.todo .addTodo div button{
    font-size:18px;
    display: inline-block;
    margin-left:5px;
    transition:0.3s;
    width:30px;
    height:30px;
    text-align: center;
    line-height:25px;
    border:none;
    outline: none;
    border-radius:50%;
    background-color: #4ea6b8;
    color: white;
    font-weight:bold;
    cursor: pointer;
}
.todo .noTodo{
    width:100%;
    margin-top:50px;
    font-size:20px;
    font-weight:bold;
    letter-spacing: 1px;
    opacity:0.5;
    margin-bottom:20px;
}
.todo .todoList{
    position: relative;
    width:100%;
    min-height:200px;
    overflow-x: hidden;
    overflow-y: auto;
}
.todo .todoList .todoItem{
    width:100%;
    margin:10px 0;
    padding:5px 10px 5px 0px;
    position: relative;
    display: flex;
    flex-direction: row;
    align-items: center;
}
.todo .todoList .todoItem div{
    margin-right:10px;
}
.todo .todoList .todoItem div i{
    cursor: pointer;
}
.todo .todoList .todoItem div i:nth-child(2),
.todo .todoList .todoItem div i:nth-child(3){
    display:none;
    color:red;
    margin:0 10px;
    width:22px;
    text-align: center;
}
.todo .todoList .todoItem div i:nth-child(2){
    margin-left:20px;
}
.todo .todoList .todoItem div i:nth-child(2){
    color: #4ea6b8;
}
.todo .todoList .todoItem.openOptions div i:nth-child(2),
.todo .todoList .todoItem.openOptions div i:nth-child(3){
    display:inline-block;
}
.todo .todoList .todoItem div i.options{
    opacity:0.5;
}
.todo .todoList .todoItem p{
    font-size:15px;
    width:calc(100% - 50px);
}
.todo .todoList .todoItem.openOptions p{
    font-size:15px;
    width:calc(100% - 150px);
}
.todo .todoList .todoItem.done p{
    opacity:0.4;
    text-decoration:line-through;
}

/* dairy */
.dairy{
    width:calc(100vw / 3);
    grid-area: dairy;
    height:100vh;
}
.dairy .det div{
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    padding:10px;
    background-color: #4ea6b8;
    max-width:400px;
    margin-bottom:10px;
}
.dairy .det div p{
    color: white;
}
.dairy .det div button{
    background: none;
    outline: none;
    border: none;
    cursor: pointer;
    color: white;
}
.dairy .det div button i{
    display: inline-block;
    margin-left:10px;
}
.dairy .det div:nth-child(2){
    padding:0;
    background-color:white;
}
.dairy .det div:nth-child(2) p{
    padding:0 10px;
    font-size:20px;
    color:#4ea6b8;
}
.dairy .det div:nth-child(2) input{
    outline:none;
    border: none;
    opacity:0.5;
}
.dairy .dairyText{
    width:100%;
    max-width:400px;
    height:calc(100% - 100px);
    position: relative;
}
.dairy .dairyText div{
    position: absolute;
    top:0px;
    left:10px;
    width:calc(100% - 20px);
    height:calc(100% - 20px);
    overflow: hidden;
}
.dairy .dairyText div span{
    display: block;
    width:100%;
    height:35px;
    border-bottom: 1px dashed #4ea6b8;
}
.dairy .dairyText textarea{
    position:absolute;
    padding:10px;
    width:100%;
    height:calc(100% - 10px);
    resize: none;
    z-index: 100;
    outline: none;
    background: none;
    border: none;
    line-height:35px;
}
.dairy .dairyText button{
    position: absolute;
    bottom:1px;
    right: 10px;
    cursor: pointer;
    font-size:14px;
    border:none;
    background-color: #4ea6b8;
    outline: none;
    padding:10px;
    color: white;
    z-index: 200;
}
/* events */

.events{
    width:calc(100vw / 3);
    grid-area: events;
}
.events .addEvent p{
    font-size:20px;
    color:#4ea6b8;
}

/* notes */
.notes{
    height:100vh;
    width:calc(100vw / 3);
    grid-area: notes;
}
.notes .addNote p{
    font-size:20px;
    color:#4ea6b8;
}
.notes .addNote div{
    margin:10px 0;
}
.notes .addNote div input{
    font-size:13px;
    outline:none;
    border:none;
    background-color: whitesmoke;
    padding:10px;
    border-radius:5px;
    display: inline-block;
    width:calc(100% - 50px);
    max-width:300px;
}
.notes .addNote div button{
    font-size:18px;
    display: inline-block;
    margin-left:5px;
    transition:0.3s;
    width:30px;
    height:30px;
    text-align: center;
    line-height:25px;
    border:none;
    outline: none;
    border-radius:50%;
    background-color: #4ea6b8;
    color: white;
    font-weight:bold;
    cursor: pointer;
}
.notes .notesList{
    width:100%;
    position: relative;
}
.notes .notesList .noNote{
    width:100%;
    height:200px;
    display: flex;
    justify-content: center;
    align-items: center;
    opacity:0.5;
}
.notes .notesList .note{
    width:100%;
    margin:10px 0;
}
.notes .notesList .note textarea{
    width:100%;
    max-width:400px;
    min-height:200px;
    max-height:100vh;
    overflow-x: hidden;
    overflow-y: auto;
    resize: none;
    outline: none;
    padding:10px;
}
.notes .notesList .note div button{
    padding:10px;
    cursor: pointer;
    background: none;
    border: none;
    outline: none;
    color:red;
}
.notes .notesList .note div button:nth-child(2){
    margin-left:20px;
    color: #4ea6b8;
}

@media only screen and (max-width: 1000px){
    .grid{
        grid-template-areas: "dairy todo" "dairy events" "notes notes";
    }
    .dairy,.events,.todo{
        width:50vw;
    }
    .notes{
        width:100vw;
        height:50vh;
    }
    .notes .notesList .note textarea{
        width:100%;
        max-width:400px;
    }
    .notes .notesList .note{
        margin:10px;
    }
}


@media only screen and (max-width: 700px){
    .grid{
        grid-template-areas: "dairy" "todo" "events" "notes";
    }
    .dairy,.events,.todo,.notes{
        width:100vw;
        height:50vh;
        padding:10px 10px 10px 20px;
    }
    .dairy .det div,
    .dairy .dairyText{
        max-width:100%;   
    }
    .notes .notesList .note{
        margin:10px 0;
    }
}